@import url('https://fonts.googleapis.com/css2?family=Ubuntu+Condensed&display=swap');
/*

  font-family: 'Ubuntu Condensed', sans-serif;

*/

p {
  margin-right: 6px;
  margin-left: 6px;
}

.App {
  max-width: 700px;  
  margin: auto;
}

h3 {
  margin: auto;
  padding: 1vh;
  font-family: 'Arial Black';
}

.safeArea {
  max-width: 100%;
  overflow-x: hidden;
}

.timeWeatherBar {
  display: flex;
  margin: 1vh;
  justify-content: space-between;
  align-items: center;
  font-family: 'Arial Black';
  color: lightgray;
}

.weatherTemp {
  display: flex;
  align-items: center;
}

.horizontalContents {
  display: flex;
  overflow: auto;
  white-space: wrap;
  margin-left: 1vh;
}

.contentSizeForToday {
  min-width: 190px;
  min-height: 190px;
  background-color: rgb(245,245,245);
  margin: 1px;
  display: flex;
  align-items: flex-end;
}

.userContentOpacityLayer {
  font-family: 'Ubuntu Condensed', sans-serif;
  min-width: 190px;
  min-height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #fff;
  background: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,.5));
}

.contentUserInfoTextLayer {
  display: flex;
  align-items: center;
  margin-left: 6px;
}

.profileImg-1 {
  width: 36px;
  height: 36px;
  border-radius: 50px;
  border: 1px solid white;
}

.naviBar {
  align-items: center;
  margin: 2vh;
  position: -webkit-sticky; /* Safari */
  position: sticky;
  top: 0;
}

.naviBarIcons {
  display: flex;
  border: 3px solid rgb(245,245,245);
  border-radius: 15px;
  background-color: #fff;
  padding: .5rem;
  margin: auto;
  justify-content: space-around;
}

.mood-select {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.tempu-text {
  font-family: 'Arial Black';
  font-size: large;
  padding: .5rem;
}

.flat-content3 {
  margin: auto;
  display: flex;
  justify-content: center;
  width: 100%;
  flex-wrap: wrap;

}

.flat-content1 {
  margin: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;
  width: 31%;
  aspect-ratio: 1;
}

.search-bar {
  display: flex;
  width: 90%;
  background-color: rgba(0,0,0,.1);
  padding: 8px;
  border-radius: 5rem;
  margin: auto;
  font-family: Arial, sans-serif;
  border-style: none;
  font-size: 14px;
  margin-top: 20px;
  margin-bottom: 20px;  
}

::placeholder {
  color: rgba(50,50,50,0.5);
  font-size: 14px;
  font-family: 'Arial';
  padding: 5px;
}

.explore-page {
  justify-content: center;
  margin-top: 20px;
  margin: auto;
}

.flat-content2 {
  display: flex;
  align-items: center;
  max-width: 100%;
  padding: 1vh;
  justify-content: center;
  flex-wrap: wrap;
}

.flat-content-two {
  display:flex;
  border: 1px solid white;
  background-color: whitesmoke;
  width: 45%;
  aspect-ratio: 1;
  justify-content: center;
}

.flat-content-opac-layer {
  font-family: 'Ubuntu Condensed', sans-serif;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 14px;
  color: #fff;
  background: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,0), rgba(0,0,0,0), rgba(0,0,0,.4));
}

.profile-texts {
  display: flex;
  font-family: 'Ubuntu Condensed', sans-serif;
  padding: 8px;
}

.single-row {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.single-content {
  display: flex;
  background-color: whitesmoke;
  width: 90%;
  aspect-ratio: 1;
  margin-top: 30px;
  justify-content: center;
  align-items: center;
  font-family: 'Ubuntu Condensed',  sans-serif;
}

.view-item-bar {
  font-family: 'Ubuntu Condensed', sans-serif;
  width: 90%;
  display: flex;
  flex-direction: row;
  margin-top: 20px;
  margin-bottom: 20px;
  justify-content: space-between;
}

.view-item-components {
  display: flex;
}

.user-info-text {
  margin-left: 10px;
}

.user-height-text {
  color:grey;
}

button {
  border-style: none;
  border-radius: 20rem;
  width: 100px;
  font-family: 'Ubuntu Condensed', sans-serif;
  color: white;
  font-size: 14px;
  background-color: rgb(75, 75, 75);
}

button:hover {
  opacity: 0.9;
}


